.headshot {
    height: 20vh;
    width: 20vh;
    border: .5vh solid #d9d9d9;
    border-radius: 5vh;
    margin: 1vh;
    display: inline-block;
    align-content: center;
    position: relative;
}

.headshot-inactive {
    height: 20vh;
    width: 20vh;
    border: 5px solid blue;
    border-radius: 5vh;
    background-color: gray;
    margin: 1vh;
    display: inline-block;
    align-content: center;
    position: relative;
}

.name-box{
    opacity: 0;
    display: flex;
    position: relative;
    text-align: center;
    border-radius: 4vh;
    font-size: 2vh;
    background-color: #6bbde4;
    overflow-y: hidden;
    z-index: 200;
    height: 100%;
    -webkit-animation: fadeOut .5s ease-in-out;
            animation: fadeOut .5s ease-in-out;
}

.name-box:hover{
    display: flex;
    background-color: #6bbde4;
    text-align: center;
    z-index: 200;
    width: 100%;
    height: 100%;
    opacity: 1;
    -webkit-animation: fadeIn .25s ease-in-out;
            animation: fadeIn .25s ease-in-out;
}

.image-box{
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 4vh;
    background-color: #6bbde4;
    transform: translateY(-100%);
    z-index: 100;
    width: 100%;
    height: 100%;
}

.image-box-inactive{
    display: flex;
    position: relative;
    border-radius: 5vh;
    background-color: #6bbde4;
    transform: translateY(-100%);
    -webkit-filter: grayscale(100%);
    -moz-filter: grayscale(100%);
    -o-filter: grayscale(100%);
    -ms-filter: grayscale(100%);
    filter: grayscale(100%); 
    z-index: 100;
    width: 100%;
    height: 100%;
}

.invisible{
    visibility: hidden;
}

.marquee-headshot{
    display: flex;
    text-align: center;
    position: relative;
    transform: translateY(2vh);
}

.name-focus{
    opacity: 0;
    color: black;
    position: relative;
    width: 100%;
    vertical-align: middle;
    height: 100%;
    z-index: 300;
    -webkit-animation: fadeOut .25s ease-in-out;
            animation: fadeOut .25s ease-in-out;
}

.filler {
    height: .5vh;
}

.name-focus:hover{
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: 300;
    -webkit-animation: fadeIn .5s ease-in-out;
            animation: fadeIn .5s ease-in-out;
}

@-webkit-keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@-webkit-keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}

@keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}