body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #1b5181;
  height: 100vh;
  width: 100vw;
  overflow-y: auto;
  overflow-x: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  position: -webkit-sticky;
  position: sticky;
  z-index: 800;
  width: 100vw;
  height: 5vh;
  top: 0;
  background-color: #151515;
  min-height: 25px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow-x: hidden;
}

.basic-button{
  color: black;
  text-decoration: none;
  background-color: #1b5181;
  border-color: #754fa0;
  margin-right: 1.5vw;
  padding: .5vh 1vw;
  cursor: pointer;
  border-radius: 1vh;
  font-weight: 600;
  justify-content: center;
}

.link-button{
  justify-content: center;
  display: flex;

}

.App-link {
  color: #1b5181;
}

.App-body{
  position: relative;
  overflow-y: visible;
  z-index: 700;
  background-color: #1b5181;
  margin-top: 1vh;
}

#main-color::-webkit-scrollbar {
  width: 1.5vw;
  background-color: yellow;
  border-width: 3px;
  border-color: silver;
  border-style: solid;
  border-radius: 20vw;
}

#main-color::-webkit-scrollbar-thumb {
  background-color: black;
  border-radius: 20vw;
	border: 2px solid #555555;
}

#main-color::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #F5F5F5;
}

.roster-page{
  overflow-x: hidden;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.route-wrapper {
  position: relative;
  z-index: 100;
  display: flex;
  justify-content: center;
  width: 100%;
}

.route-wrapper > div {
  position: absolute;
  width: 100vw;
  display: flex;
  justify-content: center;
}

.headshot {
    height: 20vh;
    width: 20vh;
    border: .5vh solid #d9d9d9;
    border-radius: 5vh;
    margin: 1vh;
    display: inline-block;
    align-content: center;
    position: relative;
}

.headshot-inactive {
    height: 20vh;
    width: 20vh;
    border: 5px solid blue;
    border-radius: 5vh;
    background-color: gray;
    margin: 1vh;
    display: inline-block;
    align-content: center;
    position: relative;
}

.name-box{
    opacity: 0;
    display: flex;
    position: relative;
    text-align: center;
    border-radius: 4vh;
    font-size: 2vh;
    background-color: #6bbde4;
    overflow-y: hidden;
    z-index: 200;
    height: 100%;
    -webkit-animation: fadeOut .5s ease-in-out;
            animation: fadeOut .5s ease-in-out;
}

.name-box:hover{
    display: flex;
    background-color: #6bbde4;
    text-align: center;
    z-index: 200;
    width: 100%;
    height: 100%;
    opacity: 1;
    -webkit-animation: fadeIn .25s ease-in-out;
            animation: fadeIn .25s ease-in-out;
}

.image-box{
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 4vh;
    background-color: #6bbde4;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    z-index: 100;
    width: 100%;
    height: 100%;
}

.image-box-inactive{
    display: flex;
    position: relative;
    border-radius: 5vh;
    background-color: #6bbde4;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    -webkit-filter: grayscale(100%);
    -moz-filter: grayscale(100%);
    -o-filter: grayscale(100%);
    -ms-filter: grayscale(100%);
    filter: grayscale(100%); 
    z-index: 100;
    width: 100%;
    height: 100%;
}

.invisible{
    visibility: hidden;
}

.marquee-headshot{
    display: flex;
    text-align: center;
    position: relative;
    -webkit-transform: translateY(2vh);
            transform: translateY(2vh);
}

.name-focus{
    opacity: 0;
    color: black;
    position: relative;
    width: 100%;
    vertical-align: middle;
    height: 100%;
    z-index: 300;
    -webkit-animation: fadeOut .25s ease-in-out;
            animation: fadeOut .25s ease-in-out;
}

.filler {
    height: .5vh;
}

.name-focus:hover{
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: 300;
    -webkit-animation: fadeIn .5s ease-in-out;
            animation: fadeIn .5s ease-in-out;
}

@-webkit-keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@-webkit-keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}

@keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}
.sidenav {
    height: 85vh;
    width: 0;
    position: absolute;
    z-index: 1000;
    top: 0;
    right: 0vw;
    background-color: #5178a2;
    overflow-x: hidden;
    max-width: 250px;
    transition: 0.5s;
    padding-top: 10vh;
    display: inline-flex;
    flex-direction: column;
    }
    
    .sidenav a {
    padding: 2vh 2vw 3vh;
    text-decoration: none;
    font-size: 25px;
    color: white;
    display: flex;
    transition: 0.3s;
    }
    
    .sidenav a:hover {
    color: #f1f1f1;
    }
    
    .filter-header {
        padding: 2vh 2vw 3vh;
        text-decoration: none;
        font-size: 25px;
        color: white;
        display: flex;
        transition: 0.3s;
    }
    
    .filter-text {
        padding: 0vh 2vw;
        margin-bottom: -2vh;
        margin-top: 1vh;
        text-decoration: none;
        font-size: 14px;
        display: flex;
        color: white;
    }
    
    .filter-header:hover {
    color: #f1f1f1;
    }
    
    .filter-input {
        text-align: left;
        width: 80%;
        max-width: 180px;
    }
    
    .filter-button {
        margin-left: 1vh;
    }

    .headshot-box{
        position: relative;
        display: inline-flex;
    }
    
    .sidenav .closebtn {
    display: flex;
    position: absolute;
    top: 0;
    left: 0px;
    font-size: 36px;
    margin-left: 0px;
    }
