@-webkit-keyframes expand {
    from {
        width: 1%;
        margin-top: 6vw;
        font-size: 0;
        height: 1%;
        opacity: 0;
    }
    to {
        width: 18em;
        margin-top: 1vh;
        font-size: inherit;
        height: 25vh;
        opacity: 1;
    }
    }

@keyframes expand {
    from {
        width: 1%;
        margin-top: 6vw;
        font-size: 0;
        height: 1%;
        opacity: 0;
    }
    to {
        width: 18em;
        margin-top: 1vh;
        font-size: inherit;
        height: 25vh;
        opacity: 1;
    }
    }
    
    @-webkit-keyframes shrink {
    from {
        width: 110.28px;
        font-size: 11px;
        height: 21px;
        opacity: 1;
    }
    to {
        width: 0;
        font-size:0px;
        height: 0;
        opacity: 0;
    }
    }
    
    @keyframes shrink {
    from {
        width: 110.28px;
        font-size: 11px;
        height: 21px;
        opacity: 1;
    }
    to {
        width: 0;
        font-size:0px;
        height: 0;
        opacity: 0;
    }
    }
    
    .disappear-button{
        width: 0%;
        height: 0%;
        opacity: 0;
        -webkit-animation: shrink 1.4s  ease-in-out;
        animation: shrink 1.4s ease-in-out;
    
    }
    
    .prepped-code{
        border-radius: 5vw;
        text-align: center;
        padding-left: 1vw;
        margin-top: 1vh;
        padding-top: 10vh;
        padding-bottom: 0vh;
        width: 18em;
        height: 25vh;
        resize: none;
        overflow-y: hidden;
        background-color: white;
        -webkit-animation: expand 1.4s  ease-in-out;
        animation: expand 1.4s ease-in-out;
        margin-bottom: 2vw;
    }
    

